.quote {
  border-radius: 40px 0px 40px 40px;
  background: #F0F2ED;
  max-width: 800px;
  display: flex;
  column-gap: 36px;
  padding: 36px 48px;
  @media (min-width: 1920px) {
    max-width: 64.5%;
  }

  &__face {
    border-radius: 100px;
    @media (min-width: 1920px) {
      width: 12.5%;
      & img {
        width: 100%;
      }
    }
  }
  &__description {
    font-weight: 300;
    & div {
      font-size: 20px;
      line-height: 180%; /* 36px */
      &:not(:last-child) {
        margin-bottom: 16px;
      }
      @media (min-width: 1920px) {
        @include adaptiv-font-fullScrean(20, 12);
      } 
    }
    & p {
      & span {
              font-size: 16px;
      line-height: 180%; /* 28.8px */
      opacity: 0.5;
      @media (min-width: 1920px) {
        @include adaptiv-font-fullScrean(20, 12);
      }
      }

      & img {
        display: none;
      }
    }
  }
}

@media (max-width: 768px) {
  .quote {
    padding: 28px 36px;
    column-gap: 24px;
    &__face {
      max-width: 64px;
      max-height: 64px;
    }
    &__description {
      & div {
        font-size: 16px;
      }
    }
  }
}
@media (max-width: $mobile-sm) {
  .quote {
    padding: 24px;
    row-gap: 12px;
    &__face {
      display: none;
    }
    &__description {
      & div {
        font-size: 12px;
      }
      & p {
        display: flex;
        & span {
          
          font-size: 12px;
        }
        & img {
          margin-right: 12px;
          max-width: 32px;
          max-height: 32px;
          width: 100%;
          display: flex;
        }
      }

    }
  }
}