.footer-case {
  &--green {
    background-color: #00AAA0;
  }
  &__container {
    max-width: 1880px;
    padding: 0px 60px;
    margin: 0 auto;
  }
  &__top {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 60px;
  }
  &__top-items {
    display: flex;
    flex-direction: column;
    row-gap: 32px;
  }
  &__top-item {
    display: flex;
    flex-direction: column;
    row-gap: 6px;
    font-size: 20px;
    font-weight: 300;
    line-height: 180%; /* 36px */
  }
  &__top-title {
    letter-spacing: -0.4px;
    color: rgba(255, 255, 255, 0.60);
  }
  &__top-list {
    padding: 0;
    color: #fff;
    list-style: none;
  }
  &__slide-container {
    position: relative;
    z-index: 22;
    width: 100%;
    height: 100%;
    max-height: 877px;
    & .swiper-slide  {
      & img {
        width: 100%;
        height: 100%;
      }
    }
    & .swiper-button-next, .swiper-button-prev {
      width: auto;
      height: 72px;
      padding: 0 32px;
      background-color: #242B2F;
      border-radius: 64px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      @include adaptiv-font-fullScrean(20, 10.608);
      font-weight: 700;
      line-height: 120%;
      column-gap: 40px;
      transition: opacity .3s;
      z-index: 2222;
      top: 71.5%;

      @media (hover: hover) {
        &:hover {
          opacity: .7;
        }
      }
      &:after {
        display: none;
      }
      & svg {
        width: 48px;
        height: 16px;
      }
    }
    & .swiper-button-next {
      right: 15.3%;
    }
    & .swiper-button-prev {
      left: 15.3%;
      & svg {
        rotate: 180deg;
      }
    }
  }
}