.todes-main {
    &__hero {
        background-image: url(../img/todes-main/main-bg.jpg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        padding-bottom: 109px;

        &-image-holder {
            text-align: center;
            padding-bottom: 36px;
        }

        &-image {
            width: 100%;
            object-fit: cover;
        }

        &-text-container {
            display: flex;
            align-items: flex-start;
        }

        &-text-group {
            &:first-child {
                @include adaptiv-font(64, 28);
                font-weight: 700;
                flex: 1;
                margin-right: 50px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                .todes-main__label {
                    margin-top: 74px;
                }
            }
            &:last-child {
                @include adaptiv-font(28, 16);
                line-height: 160%;
                max-width: 50%;
                margin-top: -17px;
            }
        }
    }

    &__section {
        display: flex;
        align-items: center;
        margin: 120px 0;

        &--offset-top-0 {
            margin-top: 0;
        }

        &-group {
            &:first-child {
                margin-right: 50px;
                flex: 1;
            }
            &:last-child {
                max-width: 50%;
                width: 50%;
                @include adaptiv-font(28, 16);
                line-height: 160%;
            }
        }
    }

    &__analitic {
        .todes-main__section {
            margin-top: 93px;
            margin-bottom: 40px;
        }
        &-steps {
            &-container {
                background: #efeef4;
                display: flex;
                padding: 0 5.51% 25px 0.68%;
                img {
                    width: 25%;
                    object-fit: cover;
                }
            }
        }
    }

    &__arch {
        margin-top: 166px;
        .todes-main__section {
            margin-bottom: 25px;
        }

        &-image-holder {
            text-align: center;
            position: relative;
            img {
                width: 100%;
                object-fit: cover;
            }
        }

        &-image-overlay {
            position: absolute;
            bottom: 0;
            z-index: 2;
            width: 100%;
            height: 24.7%;
            background: linear-gradient(
                0deg,
                #fff 0%,
                rgba(255, 255, 255, 0.9) 36.26%,
                rgba(255, 255, 255, 0.79) 64.03%,
                rgba(255, 255, 255, 0) 100%
            );
        }
    }

    &__design {
        .todes-main__section {
            margin-bottom: 40px;
        }
        .todes-main__gray-block {
            padding-bottom: 80px;
        }
        &-groups {
            display: flex;
            align-items: center;
            padding-top: 220px;
            padding-bottom: 220px;
        }

        &-group {
            &:first-child {
                margin-right: 50px;
                flex: 1;
            }
            &:last-child {
                max-width: 50%;
                width: 50%;
                @include adaptiv-font(28, 16);
                line-height: 160%;
            }
        }
    }

    &__grid {
        margin-top: 120px !important;
        margin-bottom: 120px !important;

        &-container {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 32px;
        }

        &-item {
            img {
                width: 100%;
                object-fit: cover;
            }
        }
    }

    &__video-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 80px;
    }

    &__gif-holder {
        box-shadow: -80px 80px 150px 0px rgba(108, 113, 128, 0.2);
        border-radius: 40px;
        position: relative;
        padding-bottom: 49.45%;
        margin-bottom: 25px;
        img {
            width: 100%;
            height: 100%;
            border-radius: 40px;
            position: absolute;
            object-fit: cover;
        }
    }

    &__desktop {
        .todes-main__section {
            margin-top: 200px;
            margin-bottom: 50px;
        }
    }
    .todes-main__video-block {
        padding-bottom: 80px;
    }
    &__slider {
        margin-top: 120px;
        margin-bottom: 120px;
        padding: 0 80px;
        position: relative;

        .todes-main__slide {
            position: relative;
            width: 100%;
            padding-bottom: 56%;
            border-radius: 15px;
            img {
                position: absolute;
                top: 0;
                left: 0;
                object-fit: cover;
                width: 100%;
                height: 100%;
                border-radius: 15px;
            }
        }

        &-arrow {
            position: absolute;
            top: calc(50% - 42px);
            z-index: 2;
            outline: none;

            &--prev {
                left: -40px;
            }

            &--next {
                right: -40px;
            }

            svg {
                path,
                circle {
                    transition: 300ms ease all;
                }
            }

            &:hover {
                svg {
                    circle {
                        fill: #e5e6ef;
                    }
                }
            }
        }

        &-pagination {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 35px;
            margin-bottom: 64px;
            .swiper-pagination-bullet {
                transition: 200ms ease all;
                opacity: 1;
                width: 40px;
                height: 8px;
                border-radius: 8px;
                border: 2px solid #e5e6ef;
                background: none;
                margin: 0 8px !important;

                &-active {
                    background: #242b2f;
                    border-color: #242b2f;
                }

                &:hover {
                    background: #242b2f;
                    border-color: #242b2f;
                }
            }
        }
    }

    &__desktop-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 40px;
        padding-bottom: 120px;
        img {
            display: block;
            width: 100%;
            object-fit: cover;
        }

        &-item {
            img {
                margin-bottom: 40px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__mobile {
        .todes-main__section {
            margin-bottom: 50px;
        }
    }
    &__mobile-grid {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-column-gap: 50px;
        margin-bottom: 110px;
        &-item {
            display: flex;
            flex-direction: column;
            grid-row-gap: 40px;
        }
        img {
            width: 100%;
            object-fit: cover;
        }

        &-item {
            &:nth-child(2) {
                margin-top: 270px;
            }
            &:nth-child(3) {
                margin-top: 124px;
            }
            &:nth-child(4) {
                margin-top: -90px;
            }
        }
    }

    &__kit {
        .todes-main__section {
            margin-bottom: 100px;
        }
    }

    &__kit-ui {
        position: relative;

        &-groups {
            display: flex;
            margin-bottom: 8px;
        }

        &-group {
            position: relative;
            padding-bottom: 40.117%;
            &:first-child {
                width: 77.279%;
            }
            &:last-child {
                flex: 1;
                margin-left: 8px;
            }
        }
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            object-fit: cover;
        }

        &-last {
            position: relative;
            padding-bottom: 35.06%;
        }
    }

    &__similar {
        margin: 120px 0;
        &-container {
            display: flex;
            align-items: center;
        }
        .todes-main__label {
            margin-bottom: 70px;
        }

        &-image {
            width: 66.5%;
            margin-left: -7%;
        }

        &-group {
            flex: 1;
            &-title {
                @include adaptiv-font(64, 28);
                line-height: 120%;
                font-weight: 700;
                margin-bottom: 64px;
            }
        }

        &-button {
            display: inline-flex;
            align-items: center;
            @include adaptiv-font(24, 12);
            font-weight: 700;
            border-radius: 64px;
            border: 4px solid #242b2f;
            padding: 24px 40px;
            transition: 200ms ease all;
            svg {
                margin-left: 24px;
                path {
                    transition: 200ms ease all;
                }
            }

            &:hover {
                background: #242b2f;
                color: #fff;
                svg path {
                    fill: #fff;
                }
            }
        }
    }
}

.todes-container-md {
    width: 100%;
    margin: 0 auto;
    padding: 0 12%;
    position: relative;
}

.todes-container {
    width: 100%;
    padding: 0 4.17%;
    margin: 0 auto;
}

.todes-main__label {
    @include adaptiv-font(16, 12);
    color: #fff;
    font-weight: 500;
    border-radius: 8px;
    background: #e05f50;
    padding: 6px 16px;
    display: inline-flex;
    line-height: 160%;
}

.todes-main__num {
    display: inline-flex;
    align-items: center;
    &-val {
        color: #efeef4;
        font-weight: 500;
        font-family: "Anek Tamil", sans-serif;
        line-height: 80%;
        position: absolute;
        left: 0%;
        @include adaptiv-font(540, 164);
        transform: scaleX(0.6);
    }

    &-title {
        font-weight: 700;
        @include adaptiv-font(64, 28);
        position: relative;
        z-index: 2;
        line-height: 120%;
    }
}

.todes-main__gray-block {
    padding-top: 120px;
    padding-bottom: 80px;
    background: #efeef4;
}

.todes-main__video {
    margin-bottom: 25px;
    position: relative;
    width: 100%;
    padding-bottom: 50%;
    &-holder {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &-play {
        position: absolute;
        z-index: 2;
        top: calc(50% - 62px);
        left: calc(50% - 60px);
    }

    &--sm {
        .todes-main__video-play {
            top: calc(50% - 35px);
            left: calc(50% - 35px);
        }
        .todes-main__video-play-icon {
            width: 70px;
            height: 70px;
        }
    }
}

.todes-main__note {
    display: flex;
    align-items: center;
    justify-content: center;

    &-icon {
        margin-right: 16px;
    }
    &-desc {
        color: #666b6e;
        font-size: 18px;
        font-weight: 600;
    }
}
