.project4 {
  color: #536666;
  font-family:  'Manrope', 'Raleway', "sans-serif";
  //------ -----//
  &.h2 {
    color: #536666;
    height: min-content;
    position: relative;
    z-index: 1;
    font-variant-numeric: lining-nums proportional-nums;
    letter-spacing: 0;
    display: flex;
    column-gap: 36.5px;
    align-items: center;
    & p {
      @include adaptiv-font-fullScrean(64, 28);
      font-weight: 700;
      line-height: 120%;
      // font-size: 64px;
    }
    & img {
      
      height: fit-content;
      width: 40px;
      @media (min-width: 1920px) {
        width: calc(40 * 100vw / 1920);
      }
    }
    & span {
      margin-left: 22px;
      height: 2px;
      width: 100%;
      display: block;
      background-color: #eee;
      align-self: center;
    }

    &--line {
      &::before {
        content: '';
        position: absolute;
        left: 22px;
        top: 20px;
        background-color: #F3F3F5;
        width: 100%;
        height: 2px;
      }
    }

    @media (max-width: $tablet) {
      p {
        font-size: 52px;
      }
    }
    @media (max-width: $mobile-sm) {
      // font-size: 20px;
      padding-left: 21px;
      margin-left: -21px;

      &::after {
        left: -16px;
        top: 10px;
        width: 20px;
        height: 2px;
      }
    }
  }

  //------ label-----//
  &-label {
    font-weight: 700;
    font-size: 16px;
    line-height: normal;
    background-color: #F2638A;
    color: #fff;
    padding: 8px 14px 10px;
    position: relative;
    z-index: 1;
    display: flex;
    width: fit-content;
    &::after {
      content: '';
      position: absolute;
      left: -11px;
      top: -11px;
      border: 10px solid transparent;
      border-right: 10px solid #F3F3F5;
      rotate: 45deg;
    }
    @media (min-width: 1920px) {
      @include adaptiv-font-fullScrean(16, 12)
    }
    @media (max-width: $mobile-sm) {
      padding: 7px 11px;
      font-size: 12px;
      line-height: 14px;
    }
  }
  &-content {
    display: none;
    &--active {
      display: block;
    }
    &[data-section="2"] {
      & .process {
        background-color: #FAFAFC;
        padding-bottom: 120px;
        & .container-full-screan {
          max-width: 1240px;
          width: auto;
          @media (min-width: 1920px) {
            width: 67.71%;
            max-width: fit-content;
          }
          @media (max-width: 1600px) {
            padding: 0px 56px;
          }
          @media (max-width: $mobile) {
            padding: 0px 24px;
          }
        }
        &__body {
          display: flex;
          flex-direction: column;
          row-gap: 64px;
        }
        &__column {
          display: flex;
          flex-direction: column;
          row-gap: 48px;
        }
        &__block-description {
          display: flex;
          flex-direction: column;
          row-gap: 24px;
          max-width: 800px;
          @media (min-width: 1920px) {
            max-width: 64.5%;
          }
        }
        &__title {
          font-size: 40px;
          font-weight: 600;
          line-height: 124%;
          letter-spacing: 0.8px;
          font-variant-numeric: lining-nums proportional-nums;
          @media (min-width: 1920px) {
            @include adaptiv-font-fullScrean(40, 24)
          }
        }
        &__sub-title {
          font-size: 24px;
          font-weight: 600;
          line-height: 124%; /* 29.76px */
          letter-spacing: 0.48px;
          @media (min-width: 1920px) {
            @include adaptiv-font-fullScrean(24, 16)
          }
        }
        &__text {
          font-size: 20px;
          font-weight: 300;
          line-height: 180%;
          @media (min-width: 1920px) {
            @include adaptiv-font-fullScrean(20, 12)
          }
        }
        &__block-img {
          display: flex;
          column-gap: 24px;
          & div {
            flex: 1 1 50%;
            & img {
              max-width: 100%;
              display: flex;
              width: 100%;
              &:not(:last-child) {
                margin-bottom: 16px;
              }
            }
            & p {
              max-width: 800px;
              font-size: 16px;
              font-weight: 300;
              line-height: 180%;
              opacity: 0.5;
              @media (min-width: 1920px) {
                max-width: 64.5%;
                @include adaptiv-font-fullScrean(16, 12)
              }
            }
          }
        }
        &__total {
          max-width: 800px;
          border-radius: 40px;
          border: 3px solid #EBEDE8;
          padding: 48px;
          display: flex;
          flex-direction: column;
          row-gap: 24px;
          @media (min-width: 1920px) {
            max-width: 64.5%;
          }
        }
        @media (max-width: 768px) {
          padding-bottom: 64px;
          &__text {
            font-size: 16px;
          }
        }
        @media (max-width: $mobile) {
          &__total {
            padding: 24px;
          }
        }
        @media (max-width: $mobile-sm) {
          padding-bottom: 48px;
          &__body {
            row-gap: 48px;
          }
          &__column {
            row-gap: 24px;
          }
          &__title {
            font-size: 24px;
          }
          &__sub-title {
            font-size: 16px;
          }
          &__block-img {
            flex-wrap: wrap;
            row-gap: 16px;
            & div {
              & img {
                &:not(:last-child) {
                  margin-bottom: 8px;
                }
              }
  
              & p {
                font-size: 12px;
              }
            }

          }
          &__text {
            font-size: 12px;
          }
        }
      }
    }
    &[data-section="3"] {
      padding-bottom: 64px;
      background-color: #FAFAFC;
      & .project4-content__body {
        max-width: 600px;
        margin: 0 auto;
        @media (min-width: 1920px) {
          max-width: 37.5%;
        }

        & img {
          width: 100%;
        }
      } 

    }
  }
  &-sect {
    &__tabs {
      background-color: #FAFAFC;
      padding: 139px 0 85px;
      & .project4-tabs {
          
          &__body {
            display: flex;
            justify-content: center;
            gap: 40px;
          }
          &__item {
            font-size: 24px;
            position: relative;
            cursor: pointer;
            font-weight: 700;
            line-height: 180%;
            transition: all .3s;
            padding: 14px 0px;
            opacity: 0.5;
            padding: 4px 16px;
            &--active {
              opacity: 1;
              background-color: #fff;
              border-radius: 12px;
              box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.08);
            }
            @media (min-width: 1920px) {
              @include adaptiv-font-fullScrean(24, 16)
            }
            @media (hover: hover) {
              &:hover {
                background-color: #fff;
                border-radius: 12px;
                box-shadow: 4px 12px 8px 0px rgba(0, 0, 0, 0.08);
              }
            }
            
          }
      }
      @media (min-width: 1920px) {
        padding-top: 7.24%;
        padding-bottom: 4.43%;
      }
    }
    &__devices1 {
      position: absolute;
      right: 4%;
      width: 60.46875%;
      
      &-img {
        position: absolute;
        bottom: 0;
        z-index: 1;
        filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.04));
        display: flex;
        
        & img {
          width: 100%;
        }

        &:nth-child(1) {
          width: 92.248%;
          filter: drop-shadow(0px 20px 25px rgba(0, 0, 0, 0.10)) drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.04));
          right: 0;
          margin-bottom: -1px;
        }

        &:nth-child(2) {
          width: 36.3479%;
          margin-bottom: -2%;
          right: 64%;
        }

        &:nth-child(3) {
          width: 21.36%;
          margin-bottom: -3.53%;
          right: 55%;
        }
      }
    }
    //---aboutUs------------//
    &__aboutUs {
      background-color: #FAFAFC;
      font-family:  'Manrope', 'Raleway', "sans-serif";
      padding: 51px 0px 175px;
      & .project4-aboutUs {

        @media (min-width: 1920px) {
          gap: 20.572%;
        }
        &__body {
          display: flex;
          column-gap: 40px;
          row-gap: 40px;
          position: relative;
          justify-content: space-between;
        }
        &__block {
          // max-width: 800px;
          // margin-left: 40px;
          max-width: 52%;
          margin-left: 2.0833%;
          & p {
            font-weight: 400;
            font-kerning: none;
            @include adaptiv-font-fullScrean(28, 16);
            line-height: 160%;
            color: #536666;
            margin-bottom: 40px;
            padding-left: 40px;
            position: relative;
            font-variant-numeric: lining-nums proportional-nums;
            &::after {
              content: '';
              position: absolute;
              top: 0;
              // left: -120px;
              left: -11%;
              background-image: url(../img/project4/quotes.svg);
              width: 80px;
              height: 50px;
              background-repeat: no-repeat;
              background-size: 80px;
              @media (min-width: 1920px) {
                width: 10%;
                height: -webkit-fill-available;
                background-size: contain;
                left: -11%;
              }
            }
            & span {
              display: block;
              margin: 12px 0px;

            }
          }

          & ul {
            list-style: none;
            padding: 0;
            display: flex;
            gap: 10px;
            padding-left: 40px;
            & li {
              display: flex;

              & a {
                color: #fff;
                font-weight: 500;
                @include adaptiv-font-fullScrean(16, 10);
                line-height: 160%;
                opacity: .5;
                background-color: #03afa7;
                border-radius: 10px;
                padding: 6px 16px;

                @media (hover: hover) {
                  &:hover {
                    opacity: 1;
                  }

                }
              }
            }
          }
        }

      }
    }

    //---analytics------------//
    &__analytics {
      position: relative;
      padding: 80px 0px 0px 0px;
      background-color: #F3F3F5;
      & .project4-analytics {
        &__pluses {
          position: absolute;
          right: -32%;
          top: 24%;
          width: 43.5%;
          height: 29.5%;
        }

        &__block {
          margin: 56px 0px 80px;
          display: flex;
          justify-content: space-between;
        }

        &__text {
          font-weight: 400;
          line-height: 160%;
          max-width: 800px;
          font-kerning: none;
          font-variant-numeric: lining-nums proportional-nums;
          padding-left: 80px;
          @include adaptiv-font-fullScrean(28, 16);
          @media (min-width: 1920px) {
            max-width: 100%;
            width: 50%;
            
          }
        }

        &__mindmap {
          display: flex;
          flex-direction: row;
          max-width: 100%;
          margin-left: 158px;
          position: relative;
          z-index: 2;
          @media (min-width: 1920px) {
            max-width: 75%;
          }
          & .mindmap {
            &__wrap {
              & div img {
                display: block !important;
              }
            }

            &__items-m {
              display: none;
            }

            &__img {
              position: absolute;
              right: 0;
              max-width: 100%;
              transform: translateY(0) !important;

              &--t {
                display: none;
              }
            }

            &__line {
              width: 100%;

              & img:last-child {
                display: none;
              }
            }

            &__items {
              display: flex;
              flex-direction: column;
              row-gap: 28px;
              flex: 1 1 33.333%;
              & div {
                height: 43px;
                display: flex;
                position: relative;

                & img {
                  max-width: 100%;
                  width: inherit;
                  position: absolute;
                  right: 0;
                  top: 50%;
                  transform: translateY(-50%);
                }
              }

              & span {
                font-weight: 700;
                font-size: 16px;
                line-height: 120%;
                color: #fff;
                padding: 0px 16px;
                text-align: center;
                border-radius: 4px;
                display: flex;
                align-items: center;
                z-index: 1;
                font-variant-numeric: lining-nums proportional-nums;
                font-family: 'Raleway', 'Manrope', "san-serif";
                text-wrap: nowrap;
                & br {
                  display: none;
                }
                @media (min-width: 1920px) {
                  @include adaptiv-font-fullScrean(16, 12)
                }
              }

              &:nth-child(1) {
                flex-basis: 36%;
                position: relative;
                flex-shrink: 0;
                &::after {
                  content: '';
                  position: absolute;
                  top: 22.5px;
                  right: 0;
                  width: 20%;
                  height: 98.6%;
                  background-image: url(../img/project4/mindmap-pc/left-2.png);
                  background-repeat: no-repeat;
                  background-size: 100% 100%;
                  background-position: right;
                }
                &::before {
                  content: '';
                  position: absolute;
                  top: 290px;
                  right: 16%;
                  width: 66%;
                  height: 1%;
                  background-image: url(../img/project4/mindmap-pc/left-1.png);
                  background-repeat: no-repeat;
                  background-size: cover;
                  background-position: right;
                }

                & div {
                  z-index: 1;
                  margin-top: 280px;
                }

                & span {
                  background-color: #D53863;

                }
              }

              &:nth-child(2) {
                position: relative;
                & span {
                  background-color: #00AAA0;
                  position: relative;
                  white-space: nowrap;

                  & br {
                    display: none;
                  }
                }

              }

              &:nth-child(3) {
                flex-shrink: 0;
                flex-basis: 34%;
                & span {
                  color: #536666;
                  background-color: #fff;
                }
              }
            }
          }
        }
      }

    }

    //---prototypes------------//
    &__prototypes {
      padding-top: 337px;
      padding-bottom: 76px;
      position: relative;
      background: linear-gradient(to bottom left, #F3F3F5 50%, #fff 50%);
      @media (min-width: 1920px) {
        padding-top: 17.555%;
        padding-bottom: 3.96%;
        margin-top: -6.9%;
      }
      & .project4-label::after {
        border-right-color: #fff;
      }
      & .project4.h2 {
        margin-top: 56px;
        margin-bottom: 39px;
      }

      & .project4-prototypes {
        &__text {
          padding-left: 80px;
          font-weight: 400;
          @include adaptiv-font-fullScrean(28, 16);
          line-height: 160%;
          max-width: 874px;
          z-index: 1;
          position: relative;
        }
      }
    }

    //---typography------------//
    &__typography {
      padding: 187px 0px 100px 0px;
      font-family: "Raleway", "Manrope","sans-serif";
      
      & .project4.h2 {
        margin: 62px 0px 100px 22px;
      }

      & .project4-label::after {
        border-right-color: #fff;
      }
      & .project4-typography {
        &__body {
          & h3 {
            letter-spacing: 0;
            font-weight: 900;
            line-height: 120%;
            color: #425351;
            margin-bottom: 40px;
            text-transform: uppercase;
            @include adaptiv-font-fullScrean(96, 36);
          }
    
          & h4 {
            font-weight: 500;
            @include adaptiv-font-fullScrean(48, 32);
            line-height: 120%;
            color: #425351;
            margin-bottom: 40px;
            letter-spacing: 0;
          }
          
          & p {
            font-weight: 700;
            @include adaptiv-font-fullScrean(36, 14);
            line-height: 160%;
            margin-bottom: 80px;
            color: #A9B8B8;
            letter-spacing: 3px;
    
            &:last-child {
              font-weight: 500;
              margin-bottom: 0px;
            }
          }

        }
      }

      
    }

    //---colors------------//
    &__colors {
      padding: 75px 0 100px;

      & .project4.h2 {
        margin-bottom: 100px;
      }

      & ul {
        list-style: none;
        padding: 0;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
        column-gap: 4%;
        row-gap: 60px;
        position: relative;
        & li {
          height: 8.35vw;
          min-height: 120px;
          border-right: 1px solid;
          border-bottom: 1px solid;
          display: flex;
          justify-content: flex-end;
          align-items: flex-end;
          position: relative;
          border-color: #d53863;
          background: linear-gradient(to bottom right, #d53863 50%, #fff 50%);


          &:nth-child(2) {
            border-color: #00AAA0;
            background: linear-gradient(to bottom right, #00AAA0 50%, #fff 50%);
          }

          &:nth-child(3) {
            border-color: #536666;
            background: linear-gradient(to bottom right, #536666 50%, #fff 50%);
          }

          &:nth-child(4) {
            border-color: #A9B8B8;
            background: linear-gradient(to bottom right, #A9B8B8 50%, #fff 50%);

          }

          &:nth-child(5) {
            border-color: #F3F3F5;
            background: linear-gradient(to bottom right, #F3F3F5 50%, #fff 50%);
          }

          & p {
            font-weight: 700;
            padding: 0px 20px 20px 0px;
            font-size: 20px;
            line-height: 120%;
            font-feature-settings: 'pnum'on, 'lnum'on;
          }
        }

      }
      @media (min-width: 1920px) {
        & ul {
          & li {
            & p {
              @include adaptiv-font-fullScrean(20, 12)
            }
          }
        }
      }
    }

    //---elements------------//
    &__elements {
      padding: 75px 0px 100px;

      & .project4.h2 {
        margin-bottom: 32px;
        display: flex;
      }

      & .elements__img {
        padding-left: 39px;
        display: flex;
        & img {
          max-width: 100%;
          width: 100%;
          height: auto;
        }
      }
    }

    //---design------------//
    &__design {
      padding: 78px 0 100px;
      position: relative;

      & .project4-label::after {
        border-right-color: #fff;
      }

      & .project4-design {

        &__pluses {
          position: absolute;
          top: 366px;
          left: -54px;
          z-index: -1;
        }

        &__block {
          padding: 56px 0px 100px 0px;
          display: flex;
          justify-content: space-between;
          row-gap: 33px;
          & .project4.h2 {
            align-items: flex-start;
            & img {
              margin-top: calc(#{36 + px} + (100vw / #{1920}));
            }
            
          }
        }
        &__text {
          max-width: 800px;
          font-weight: 400;
          @include adaptiv-font-fullScrean(28, 16);
          line-height: 160%;
          padding-top: 19px;
          padding-left: 76.5px;
        }

        &__img {
          z-index: 2;
          position: relative;
          padding: 0 0 150.5%;

          & img {
            filter: drop-shadow(0px 24.375px 32.5px rgba(56, 56, 56, 0.15));
            max-width: 100%;
            height: auto;
            position: absolute;
            width: 100%;
          }
        }

        &__hover1 {
          margin-right: 55%;

          & img {
            position: relative;
            margin-left: 64%;
            margin-top: 179%;
            width: 51%;
            filter: drop-shadow(13px 39.8125px 19.5px rgba(34, 101, 101, 0.06)) drop-shadow(13px 37.375px 39px rgba(34, 101, 101, 0.2));
          }
        }

        &__hover2 {
          margin-right: 64%;

          & img {
            position: relative;
            margin-left: 15%;
            margin-top: 67%;
            width: 70%;
            filter: drop-shadow(13px 39.8125px 19.5px rgba(34, 101, 101, 0.06)) drop-shadow(13px 37.375px 39px rgba(34, 101, 101, 0.2));
          }
        }
      }
    }

    //---desktop------------//
    &__desktop {
      margin-top: -1003px;
      padding-top: 1069px;
      padding-bottom: 113px;
      background-color: #00AAA0;
      position: relative;
      padding-left: 80px;
      &::after {
        content: '';
        position: absolute;
        bottom: -106px;
        left: 0;
        width: 100%;
        height: 760px;
        background: linear-gradient(to bottom right,#00AAA0 50%,#fff 0);
        @media (min-width: 1920px) {
          height: 39.6vw;
          bottom: -1.22%;
        }
      }

      & .project4-desktop {
        &__pluses1 {
          position: absolute;
          top: -337px;
          left: -29px;
        }

        &__pluses2 {
          position: absolute;
          bottom: 410px;
          left: -36px;
        }

        &__text {
          font-weight: 400;
          @include adaptiv-font-fullScrean(28, 16);
          line-height: 160%;
          max-width: 800px;
          margin-bottom: 61px;
          margin-left: 160px;
          color: #fff;
          font-kerning: none;
          font-variant-numeric: lining-nums proportional-nums;
        }

        &__block {
          display: flex;
          gap: 60px;
          z-index: 1;
          position: relative;
        }

        &__left {
          display: flex;
          flex-direction: column;
          gap: 58px;
          padding-top: 101px;
          position: relative;
          flex: 1 0 49%;
          &::after {
            content: "DESKTOP";
            top: -9px;
            left: -41px;
            font-weight: 900;
            position: absolute;
            font-size: 128px;
            line-height: 120%;
            color: #fff;
            opacity: .3;
            font-family: 'Raleway', 'Manrope', "san-serif";
            @media (min-width: 1920px) {
              @include adaptiv-font-fullScrean(128, 30);
              top: -0.115%;
              left: -4.2%;
            }
          }

          & img {
            border-radius: 30px;
            max-width: 100%;
            height: auto;
            z-index: 1;
            filter: drop-shadow(-11.8471px 17.7707px 5.92356px rgba(34, 101, 101, 0.06)) drop-shadow(-11.8471px 11.8471px 35.5414px rgba(34, 101, 101, 0.2));
          }
        }

        &__right {
          flex: 1 1 50%;
          display: flex;
          flex-direction: column;
          gap: 59.25px;
          
          & img {
            border-radius: 30px 0px 0px 30px;
            max-width: 100%;
            height: auto;
            filter: drop-shadow(-11.8471px 17.7707px 5.92356px rgba(34, 101, 101, 0.06)) drop-shadow(-11.8471px 11.8471px 35.5414px rgba(34, 101, 101, 0.2));
          }
        }
      }
    }

    //---ICONS------------//
    &__icons {
      padding: 86px 0px 0px;

      & .project4-icons {
        &__grid {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
          @media (min-width: 1920px) {
            grid-template-columns: repeat(auto-fill, minmax(16%, 1fr));
          }
        }
